.containerSkills{
    background-color: #111827;
    margin-top: 15%;
    height: auto
}
#titleSkills{
    width: 100%;
    min-height: 150px;
    font-family: 'Kanit', sans-serif;
    font-size: 75px;
    font-weight: 600;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    background-color: #111827;
    text-shadow: 0 0 50px #ebff05;
    margin-bottom: 0;
}

#hrTitleSkills{
    width: 20%;
    border-color: #ebff05;
    box-shadow: #ebff05  0 0 15px ;
}

.skills{
    width: 100%;
    background-color: #111827;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.containerCanva{
    /* background-color: white; */
    border-radius: 50%;
}


.iconCloud{
    display: flex;
    flex-direction: column;
    color: white;
    width: 50%;
    padding: 10%;
    /* justify-content: space-around; */
    /* transform: scale(0.8); */
}

.iconCloudTitle{
    font-size: 35px;
    margin-top: 10%;
    height: 50px;
    color: rgba(0, 255, 255, 0.502);
    text-shadow: rgba(0, 255, 255, 0.502) 0px 0px 15px;
}

/* -------------------------------------------- */
.skillsText{
    width: 50%;
    height: 100%;
}

#skillsTextEffect{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 15%;
}




@media (max-width: 1000px) {
    
    .skills{
        flex-direction: column;
    }
    .iconCloud{
        width: 100%;
        padding: 0;
        margin-top: 10%;
        margin-bottom: 10%;
        align-items: center;
    }
    .iconCloudTitle{
        font-size: 25px;
    }

    .skillsText{
        width: 80%;
        height: 500px;
        display: flex;
        justify-content: center;
    }
    
    #skillsTextEffect{
        font-size: 15px !important;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        width: 100%;
        padding: 0;
    }
}

@media (max-width: 450px) {
    #titleSkills{
        font-size: 35px;
    }
    .skillsText{
        height: 400px;
    }
    #skillsTextEffect{
        font-size: 13px !important;
    }
}
