body {
  background: #111827;
  font-size: 14px;
  margin: 0;
  padding: 0;
}

body::-webkit-scrollbar {
  width: 9px;
  background-color: #111827;
}

body::-webkit-scrollbar-thumb {
  width: 9px;
  background: #ebff05;
  border-radius: 10px;
}

.swiper {
  background-color: #111827;
  width: 100%;
  height: 400px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}